@import url(https://fonts.googleapis.com/css?family=Sen&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sen&display=swap);
.profile-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-family: 'Sen', sans-serif;
}

.box-profile {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	font-family: 'Sen', sans-serif;
}

.box-profile img {
	height: 10em;
	width: 10em;
	border-radius: 50%;
}

.MuiTypography-body1 {
	font-family: 'Sen', sans-serif;
	font-size: 1.2em;
	color: rgb(194, 72, 157);
}

nav {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-top: 1em;
}

.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-family: 'Sen', sans-serif;
}

#nav-button,
a {
	padding: 0.5em;
	background: none;
	text-decoration: none;
	border: 1px solid black;
	border-radius: 5px;
	font-size: 1em;
	color: white;
	background-color: rgb(194, 72, 157);
	font-family: 'Sen', sans-serif;
}

a:hover,
#nav-button:hover {
	background-color: blueviolet;
	color: white;
	cursor: pointer;
}

#alert-dialog-description,
.MuiTypography-h6 {
	font-family: 'Sen', sans-serif;
}

#players-title {
	font-size: 1.5em;
	font-weight: bold;
	color: rgb(194, 72, 157);
}

